import {mapGetters, mapMutations} from "vuex";
import axios from "axios";

export default {
  props: {
    open: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      userinfo: {}
    };
  },
  activated() {
    this.getProfile();
  },
  mounted() {
    this.getProfile();
  },
  methods: {
    toProfile() {
      this.$router.push("/profile/" + this.user.qq);
    },
    toSetting() {
      this.$router.push("/setting");
    },
    getProfile() {
      axios
        .get("/api/getprofile", {
          params: {
            qq: this.user.qq
          }
        })
        .then(response => {
          console.log(response);
          this.userinfo = response.data;
        })
        .catch(err => {
          console.log;
        });
    }
  },
  computed: {
    style() {
      return `background:url("/api/background/${this.userinfo.background}") no-repeat center center;background-size:cover`;
    },
    ...mapGetters(["weather", "user"])
  }
};



